import React, { useCallback, useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import Editor from '../editor/editor';
import Preview from '../preview/Preview';
import Footer from '../footer/footer';
import Header from '../header/header';
import styles from './maker.module.css'

const Maker = ({FileInput , authService, cardRepository}) => {

    //useNavigation   ; state !
    const navigation = useNavigate();
    const location = useLocation();
    const locationData = location?.state;

    //Data Object 
    //   object !!  project  ->    !
    const [ cards, setCards ] = useState({});
    const [userId, setUserId] = useState(locationData && locationData.id);

    //
    /*header onLogout   onLogout   
    header memo  !! 
    => useCallback 
    
       
    =>   callback   authService   
    */
    const onLogout = useCallback(() => {
        authService.logout();
    },[authService])

    //firebase    (useEffect   .)
    useEffect(()=> {
        if(!userId) {
            return;
        }
        //cardRepository onUpdate :    
        /*
          
        const a = calculate; //  ref ,    a calculate  
        const b = calculate(); // calculate       5 b 
        
        */
        const stopSync =  cardRepository.syncCards(userId, cards => {
            setCards(cards);
        })
        //useEffect return  : unmount return  
        //resource memory   return .
        //stopSync unmount    . => cardRepository return
        //useEffect return  function   !
        //return stopSync()      ,stopSync     sync   :)
        return () => stopSync();
    },[userId, cardRepository])


    //      : ( authState  )
    useEffect(()=> {
        authService.onAuthChange(user => {
            if(user) {
                setUserId(user.uid)
            }else {
                //   , setUserId
                navigation('/')
            }
        })
    },[authService, userId, navigation])


    /*
      & 
        
            elli {
                id : 1,
                text: 'ellie'
            }  ?
    (1)ellie.id
    (2)ellid['id']  value  .

    (2) ,      
        id key  & card object value .
    =>  updated[card.id] = card;
    */
    const creatOrUpdateCard = card => {
        /*
        {...data}     (   ?)
          setState     
        setStateAction     : 
        */
        setCards(cards => {
            //setCards   cards  
            const updated = {...cards};
            updated[card.id] = card;
            return updated;
        });
        cardRepository.saveCard(userId, card);
    };

    //
    const deleteCard = card => {
        setCards(cards => {
            const updated = {...cards};
            delete updated[card.id];
            return updated;
        })
        cardRepository.removeCard(userId,card);
    };

    return (
        <section className={styles.maker}>
            <Header onLogout={onLogout}/>
                <div className={styles.container}>
                    <Editor
                        FileInput={FileInput}
                        cards={cards} 
                        addCard={creatOrUpdateCard} 
                        updateCard={creatOrUpdateCard} 
                        deleteCard={deleteCard}/>
                    <Preview  cards={cards}/>
                </div>
            <Footer/>
        </section>
    );
};

export default Maker;